.AboutArea {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.first-section h1,
.first-section h2,
.first-section h3,
.first-section h4 {
    margin: 0px;
}

.AboutArea>* {
    text-align: center;
    margin: 0px 100px;
    font-weight: 300;
}

.AboutArea .h1 {
    margin-top: 40px;
    font-weight: 400;
}

.AboutArea .profile-pic {
    width: 400px;
    border-radius: 50%;
    margin: 25px 0px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.AboutArea h2 {
    font-weight: 400;
    font-size: 20px;
}

.AboutArea h3 {
    margin-top: 0px;
    font-weight: 500;
    font-size: 36px;
}

.AboutArea h3 span {
    color: #1a5785;
    font-size: 36px;
}

.AboutArea h4 {
    letter-spacing: 2px;
    color: #1a5785;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 40px;
}

.AboutArea p {
    margin: 0px 100px;
    line-height: 1.5em;
}

.about-grid-container p::first-letter {
    font-size: 45px;
    font-weight: 400;
    vertical-align: bottom;
    line-height: 1.05em;
}

.AboutArea .about-grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: fit-content;
    scroll-margin-top: 70px;
}

.AboutArea .about-grid-container p {
    font-size: 20px;
    text-align: justify;
}

.about-grid-container span {
    font-weight: 600;
}

.chevron-holder {
    margin-top: 35px;
    margin-bottom: 30px;
}

.chevron-holder a {
    display: flex;
    align-items: center;
    height: 50px;
}

.icon {
    width: 40px;
    padding: 0px 6px;
}

.scroll-down {
    display: block;
    text-align: center;
    margin: auto;
    font-size: 20px;
    z-index: 100;
    text-decoration: none;
    text-shadow: 0;
    width: 13px;
    height: 13px;
    border-bottom: 3px solid #1a5785;
    border-right: 3px solid #1a5785;
    z-index: 9;
    left: 50%;
    -webkit-transform: translate(-50%, 0%) rotate(45deg);
    -moz-transform: translate(-50%, 0%) rotate(45deg);
    transform: translate(-50%, 0%) rotate(45deg);
    -webkit-animation: fade_move_down 4s ease-in-out infinite;
    -moz-animation: fade_move_down 4s ease-in-out infinite;
    animation: fade_move_down 4s ease-in-out infinite;
}

.thanks_for_reading {
    width: 100%;
    margin-top: 20px;
    margin-bottom: -4px;
}

@-webkit-keyframes fade_move_down {
    0% {
        -webkit-transform: translate(0, -10px) rotate(45deg);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        -webkit-transform: translate(0, 10px) rotate(45deg);
        opacity: 0;
    }
}

@-moz-keyframes fade_move_down {
    0% {
        -moz-transform: translate(0, -10px) rotate(45deg);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        -moz-transform: translate(0, 10px) rotate(45deg);
        opacity: 0;
    }
}

@keyframes fade_move_down {
    0% {
        transform: translate(0, -10px) rotate(45deg);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        transform: translate(0, 10px) rotate(45deg);
        opacity: 0;
    }
}